import { FieldData, FieldValidation, InputError, DefaultLocation, MarkerSettings, MasterDetailDetails } from '.';
import { CropperSettings } from 'ng2-img-cropper';

export class Field {
  fieldType: string;

  name: string;

  sectionId: number;

  hidden: boolean;

  data: FieldData;

  validation: FieldValidation;

  validationErrors: Array<InputError>;

  label: string;

  tooltip: string;

  eventTriggers: any;

  cssClasses: any;

  readonly: boolean;

  disabled: boolean;

  placeholder: string;

  autoComplete: string;

  // datetime - hijri - time
  minDate: string;

  maxDate: string;

  defaultDate: string;

  minDateValue: Date;

  maxDateValue: Date;

  defaultDateValue: Date;

  yearRange: string;

  setTime: string;

  showIcon: boolean;

  dateFormat: string;

  monthNavigator: boolean;

  yearNavigator: boolean;

  hourFormat: string;

  showTime: boolean;

  displayDateFormat: string;

  formDataDateFormat: string;

  displayMonths: number;

  outsideDays: string;

  showWeekdays: boolean;

  showWeekNumbers: boolean;

  // location
  defaultLocation: DefaultLocation;

  markerSettings: MarkerSettings;

  zoom: number;

  zoomControl: boolean;

  // recaptcha
  siteKey: string;

  size: string;

  theme: string;

  // info
  htmlSnippet: string;

  // imagecropper
  cropperSettings: CropperSettings;

  // rating
  iconCancelClass: string;

  iconOnClass: string;

  iconOffClass: string;

  cancelIcon: boolean;

  starNumber: number;

  // printing
  html2CanvasIgnore: boolean;

  // masterdetail
  details: MasterDetailDetails;

  // select
  enablefilter: boolean;

  // chips
  addOnTab: boolean;

  addOnBlur: boolean;

  // editor
  height: string;

  showToolbar: boolean;

  // mask
  slotChar: string;

  autoClear: boolean;

  unmask: boolean;

  characterPattern: string;

  // fileupload
  auto: boolean;

  mode: string;

  multiple: boolean;

  buttons: any;
}
