import { Component, Inject } from '@angular/core';
import { MatDialogRef } from '@angular/material';
import { MAT_DIALOG_DATA } from '@angular/material';
import { CLOCK_TYPE, ITime } from '../w-clock/w-clock.component';

@Component({
  styles: [`.w-timepicker-dialog{padding:0;margin:-24px;width:calc(100% + 48px)}`],
  template: `<div mat-dialog-content class="w-timepicker-dialog">
  <ntw-time [color]="color" [userTime]="userTime" (reverted)="revert()" (submitted)="submit()"></ntw-time>
</div>
`
})
export class WTimeDialogComponent {
  public userTime: ITime;
  private VIEW_HOURS = CLOCK_TYPE.HOURS;
  private VIEW_MINUTES = CLOCK_TYPE.MINUTES;
  private currentView: CLOCK_TYPE = this.VIEW_HOURS;
  public color: string;

  constructor(
    @Inject(MAT_DIALOG_DATA) private data: UserTimeData,
    private dialogRef: MatDialogRef<WTimeDialogComponent>) {
    this.userTime = data.time;
    this.color = data.color;
  }

  revert() {
    this.dialogRef.close(-1);
  }

  submit() {
    this.dialogRef.close(this.userTime);
  }
}

export class UserTimeData {
  time: ITime;
  color: string;
}
