import { Injectable } from '@angular/core';
import * as FileSaver from 'file-saver';
import * as XLSX from 'xlsx';

const excelExtension = '.xlsx';
const pdfType = 'application/pdf';
const pdfExtension = '.pdf';

declare let html2canvas, pdfMake;

@Injectable({ providedIn: 'root' })
export class ExportService {
  constructor() { }

  /** @description Exports a json object as an excel file.
   * @param any json The json object.
   * @param string fileName The excel file name.
   * @param boolean rtl Whether right to left or left to right.
   * @param boolean skipHeader Whether to skip the header.
  */
  public exportAsExcel(json: any[], fileName: string, rtl: boolean = true, skipHeader: boolean = false) {
    // generate worksheet
    const worksheet: XLSX.WorkSheet = XLSX.utils.json_to_sheet(json, { skipHeader: skipHeader });

    const workbook: XLSX.WorkBook = XLSX.utils.book_new();

    if (!workbook.Workbook) {
      workbook.Workbook = {};
    }

    if (!workbook.Workbook.Views) {
      workbook.Workbook.Views = [];
    }

    if (!workbook.Workbook.Views[0]) {
      workbook.Workbook.Views[0] = {};
    }

    workbook.Workbook.Views[0].RTL = rtl;

    // generate workbook and add the worksheet
    XLSX.utils.book_append_sheet(workbook, worksheet, 'data');

    // save to file *
    XLSX.writeFile(workbook, fileName + '_' + new Date().getTime() + excelExtension);
  }

  /** @description Exports an html string as pdf.
    * @param string html The html to print.
    * @param Array<number> margins The pdf page margins.
    * @param string fileName The pdf file name.
    * @param any size The pdf size.
  */
  public exportAsPdf(html: string, fileName: string, size: any = 'A4', margins: Array<number> = [0, 0]) {
    if (html) {
      html2canvas(html).then(canvas => {
        const dataUri = canvas.toDataURL();

        const docDefinition = {
          pageSize: size,
          pageMargins: margins,
          content: [
            {
              image: dataUri
            }
          ]
        };

        pdfMake.createPdf(docDefinition).getBlob((file) => {
          this.promptFileSave(file, pdfType, fileName, pdfExtension);
        });
      });
    }
  }

  /** @description Exports a file as pdf.
   * @param any buffer The file buffer.
   * @param string fileType The file mime type.
   * @param string fileName The file name.
   * @param string fileExtension The file extension.
  */
  public promptFileSave(buffer: any, fileType: string, fileName: string, fileExtension: string) {
    const data: Blob = new Blob([buffer], {
      type: fileType
    });

    FileSaver.saveAs(data, fileName + '_' + new Date().getTime() + fileExtension);
  }
}
