export class Marker {
  latitude: number;

  longitude: number;

  draggable: boolean;

  infoHtml: string;

  eventTriggers: any;

  constructor(latitude: number, longitude: number, draggable: boolean, infoHtml?: string) {
    this.latitude = latitude;

    this.longitude = longitude;

    this.draggable = draggable;

    this.infoHtml = infoHtml;
  }
}
