export class Section {
  id: number;

  name: string;

  legend: string;

  isActive: boolean;

  hideButtons: Array<string>;

  constructor(id: number, name: string, legend: string, isActive: boolean) {
    this.id = id;

    this.name = name;

    this.legend = legend;

    this.isActive = isActive;
  }
}
